@import url(https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;600;700&display=swap);
body{background:#f6f7fad9;color:#333333;font-family:sans-serif}*{box-sizing:border-box;margin:0;padding:0}

.app{max-width:1200px;margin:0 auto;min-height:100vh;width:100%;padding:80px 0;display:flex;flex-direction:column;justify-content:center}@media (max-width: 1300px){.app{max-width:100%;padding:60px 5%}}

.search-bar{width:100%;padding:0 12px}.search-bar .title-block{align-self:center;display:flex;align-items:center}.search-bar .title-block .logo{width:60px;height:60px;max-width:100%;margin-right:20px}.search-bar .title-block .details .title{font-size:28px;line-height:32px;font-family:inherit;font-weight:bold;letter-spacing:-1px}@media (max-width: 768px){.search-bar .title-block .details .title{font-size:26px;line-height:30px}}@media (max-width: 599px){.search-bar .title-block .details .title{font-size:24px;line-height:28px}}.search-bar .title-block .details p{font-size:18px;line-height:22px;color:#777777}.search-bar .github-search{margin:20px 0;display:flex}.search-bar .github-search .search-text{height:50px;width:30%}@media (max-width: 1200px){.search-bar .github-search .search-text{width:40%}}@media (max-width: 768px){.search-bar .github-search .search-text{width:50%;height:45px}}.search-bar .github-search .search-category{width:16%;margin-left:20px}@media (max-width: 1200px){.search-bar .github-search .search-category{width:20%}}@media (max-width: 768px){.search-bar .github-search .search-category{width:30%}}.search-bar .github-search select{word-break:break-all;white-space:nowrap;overflow:hidden;max-width:101%;text-overflow:ellipsis}.search-bar .github-search input,.search-bar .github-search select{font-size:18px;color:#999999;border:1px solid #ccc;padding:10px 20px}@media (max-width: 1200px){.search-bar .github-search input,.search-bar .github-search select{font-size:17px}}@media (max-width: 768px){.search-bar .github-search input,.search-bar .github-search select{font-size:16px}}.search-bar .github-search input ::-webkit-input-placeholder, .search-bar .github-search select ::-webkit-input-placeholder{color:inherit}.search-bar .github-search input :-ms-input-placeholder, .search-bar .github-search select :-ms-input-placeholder{color:inherit}.search-bar .github-search input ::-ms-input-placeholder, .search-bar .github-search select ::-ms-input-placeholder{color:inherit}.search-bar .github-search input ::placeholder,.search-bar .github-search select ::placeholder{color:inherit}.search-bar .github-search input:focus,.search-bar .github-search select:focus{outline:none;box-shadow:none}

.search-result{margin-top:40px;position:relative;min-height:100vh}@media (max-width: 768px){.search-result{margin-top:30px}}.search-result ul{list-style:none;display:flex;flex-wrap:wrap}.search-result ul li{width:33.33%;padding:12px}@media (max-width: 768px){.search-result ul li{width:50%}}@media (max-width: 599px){.search-result ul li{width:100%}}

.user{background:white;box-shadow:0px 1px 5px 0 rgba(0,0,0,0.2);border-radius:8px;text-align:center;width:100%;position:relative;height:100%;display:flex;flex-direction:column;transform:scale(1);cursor:pointer;transition:all 0.2s ease-in-out}.user:hover{transform:scale(1.06);z-index:2}.user:hover .bio:before{right:0;left:0}.user a{text-decoration:none;color:inherit;height:100%;display:flex;flex-direction:column}.user .account{padding:25px 20px;display:flex;flex-direction:column;align-items:center;flex:1 1 auto}@media (max-width: 1200px){.user .account{padding:20px}}.user .account>img{width:120px;height:120px;border-radius:50%;margin:0 auto;display:block;border:3px solid #ccc}@media (max-width: 1200px){.user .account>img{width:100px;height:100px}}@media (max-width: 768px){.user .account>img{width:90px;height:90px}}.user .name{margin-top:15px;font-size:24px;line-height:28px;word-break:break-all}@media (max-width: 1200px){.user .name{font-size:22px;line-height:26px}}.user .follow-count{display:flex;margin-top:10px;font-size:16px;line-height:20px}@media (max-width: 1200px){.user .follow-count{font-size:16px;line-height:20px}}.user .follow-count p{color:#777;font-weight:700}.user .bio{padding:20px;border-top:1px solid #f2f5f6;position:relative}@media (max-width: 1200px){.user .bio{padding:15px 20px}}.user .bio:before{content:"";display:block;height:3px;background:#008bff;position:absolute;bottom:0;right:51%;left:51%;z-index:-1;transition:all 0.3s ease-in-out}.user .bio .detailView{display:block;color:#008bff;text-decoration:none;cursor:pointer;font-weight:600;font-size:16px;line-height:20px}@media (max-width: 1200px){.user .bio .detailView{font-size:14px;line-height:18px}}.user .bio .detailView:hover,.user .bio .detailView:active{text-decoration:underline}

.repository{background:white;box-shadow:0px 1px 5px 0 rgba(0,0,0,0.2);border-radius:8px;text-align:center;width:100%;position:relative;height:100%;display:flex;flex-direction:column;transform:scale(1);cursor:pointer;transition:all 0.2s ease-in-out}.repository:hover{transform:scale(1.06);z-index:2}.repository:hover .bio:before{right:0;left:0}.repository a{text-decoration:none;color:inherit;height:100%;display:flex;flex-direction:column}.repository .star{position:absolute;display:flex;right:20px;top:20px;align-items:center}.repository .star img{width:22px;height:auto;margin-right:4px}.repository .star p{font-family:sans-serif}.repository .account{padding:25px 20px;display:flex;flex-direction:column;align-items:center;flex:1 1 auto}@media (max-width: 1200px){.repository .account{padding:20px}}.repository .account>img{border-radius:50%;margin:0 auto;display:block;width:70px;height:70px;border:0}@media (max-width: 1200px){.repository .account>img{width:60px;height:60px}}.repository .description{color:#777777;font-size:18px;line-height:22px;margin-top:10px;max-height:60px;white-space:pre-wrap;overflow:hidden;text-overflow:ellipsis;max-width:100%}@media (max-width: 1200px) and (min-width: 600px){.repository .description{font-size:16px;line-height:20px;margin-top:8px}}.repository .name{margin-top:15px;font-size:24px;line-height:28px;word-break:break-all}@media (max-width: 1200px){.repository .name{font-size:22px;line-height:26px}}.repository .bio{border-top:1px solid #f2f5f6;position:relative;display:flex;align-items:center;padding:15px 20px}@media (max-width: 768px){.repository .bio{padding:12px 20px}}@media (max-width: 599px){.repository .bio{justify-content:center}}.repository .bio:before{content:"";display:block;height:3px;background:#333333;position:absolute;bottom:0;right:51%;left:51%;z-index:-1;transition:all 0.3s ease-in-out}.repository .bio img{width:45px;height:45px;border-radius:50%;margin-right:10px;border:2px solid #ccc}@media (max-width: 1200px){.repository .bio img{width:35px;height:35px;margin-right:8px}}@media (max-width: 599px){.repository .bio img{margin-right:10px}}.repository .bio .login{font-size:20px;line-height:24px;word-break:break-all}@media (max-width: 1200px) and (min-width: 600px){.repository .bio .login{font-size:18px;line-height:22px}}

.loading{width:100%;height:100%;display:flex;align-items:center;justify-content:center;position:absolute;background:#f7f8fa;z-index:2;left:0}.lds-spinner{color:official;display:inline-block;position:absolute;top:20%;width:80px;height:80px}.lds-spinner div{transform-origin:40px 40px;-webkit-animation:lds-spinner 1.2s linear infinite;animation:lds-spinner 1.2s linear infinite}.lds-spinner div:after{content:" ";display:block;position:absolute;top:3px;left:37px;width:6px;height:18px;border-radius:20%;background:#333333}.lds-spinner div:nth-child(1){transform:rotate(0deg);-webkit-animation-delay:-1.1s;animation-delay:-1.1s}.lds-spinner div:nth-child(2){transform:rotate(30deg);-webkit-animation-delay:-1s;animation-delay:-1s}.lds-spinner div:nth-child(3){transform:rotate(60deg);-webkit-animation-delay:-0.9s;animation-delay:-0.9s}.lds-spinner div:nth-child(4){transform:rotate(90deg);-webkit-animation-delay:-0.8s;animation-delay:-0.8s}.lds-spinner div:nth-child(5){transform:rotate(120deg);-webkit-animation-delay:-0.7s;animation-delay:-0.7s}.lds-spinner div:nth-child(6){transform:rotate(150deg);-webkit-animation-delay:-0.6s;animation-delay:-0.6s}.lds-spinner div:nth-child(7){transform:rotate(180deg);-webkit-animation-delay:-0.5s;animation-delay:-0.5s}.lds-spinner div:nth-child(8){transform:rotate(210deg);-webkit-animation-delay:-0.4s;animation-delay:-0.4s}.lds-spinner div:nth-child(9){transform:rotate(240deg);-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.lds-spinner div:nth-child(10){transform:rotate(270deg);-webkit-animation-delay:-0.2s;animation-delay:-0.2s}.lds-spinner div:nth-child(11){transform:rotate(300deg);-webkit-animation-delay:-0.1s;animation-delay:-0.1s}.lds-spinner div:nth-child(12){transform:rotate(330deg);-webkit-animation-delay:0s;animation-delay:0s}@-webkit-keyframes lds-spinner{0%{opacity:1}100%{opacity:0}}@keyframes lds-spinner{0%{opacity:1}100%{opacity:0}}

